<template>
  <div class="panel-group" id="accordion" role="tablist" ref="affixEl">
    <div class="panel panel-default">
      <!-- BEGIN  -->
      <div class="panel-heading" role="tab">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#start">
            
          </a>
        </h4>
      </div>
      <div id="start" class="panel-collapse collapse in" role="tabpanel">
        <div class="panel-body">
          <router-link to="/start/summary"></router-link>
          <router-link to="/start/intro"></router-link>
        </div>
      </div>
      <!-- END  -->

      <!-- BEGIN  -->
      <div class="panel-heading" role="tab" style="border-top: solid 1px #eaeaea">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#pageAccess">
            
          </a>
        </h4>
      </div>
      <div id="pageAccess" class="panel-collapse collapse" role="tabpanel">
        <div class="panel-body">
          <router-link to="/cloud/doc"></router-link>
          <router-link to="/cloud/appendix"></router-link>
        </div>
      </div>
      <!-- END  -->

      <!-- BEGIN  -->
      <div class="panel-heading" role="tab" style="border-top: solid 1px #eaeaea">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#serviceAccess">
            
          </a>
        </h4>
      </div>
      <div id="serviceAccess" class="panel-collapse collapse" role="tabpanel">
        <div class="panel-body">
          <router-link to="/msgHead"></router-link>
          <router-link to="/equityview/doc"></router-link>
          <router-link to="/loan/doc"></router-link>
          <router-link to="/repay/doc"></router-link>
          <router-link to="/survival/doc"></router-link>
          <router-link to="/expira/doc"></router-link>
          <router-link to="/contact/doc"></router-link>
          <router-link to="/grant/doc"></router-link>
        </div>
      </div>
      <!-- END  -->

    </div>
  </div>
</template>
<script>
export default {
  name: 'Siderbar',
  mounted () {
      this.$nextTick(() => {
          var affixEl = this.$refs.affixEl;
          $(affixEl).affix({
            offset: {
              top: 0
            }
          })
      })
  }
}
</script>
<style lang="stylus">
.panel-group
  width: 160px
  .panel-heading
    .panel-title
      a
        display: block
  .panel-collapse
    .panel-body
      padding: 0
      a
        display: block
        padding: 8px 16px
        color: #555
        &:hover
          background-color: #f8f8f8
        &:not(:last-child)
          border-bottom: solid 1px #ddd
    .router-link-exact-active
      border-left: solid 2px #009b63
      color: #009b63 !important

</style>


