<template>
    <div>        
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody v-if="!hasSlot">
                <tr>
                   <td colspan="5" class="bg-danger text-center"> +  <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <tr v-if="tableData.length > 0" v-for="item in tableData">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
                <tr>
                   <td colspan="5" class="bg-danger text-center"> + <span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
            <tbody v-if="hasSlot">
                <tr v-if="!isNull">
                   <td colspan="5" class="bg-danger text-center"> +  <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <slot></slot>
                <tr v-if="!isNull">
                   <td colspan="5" class="bg-danger text-center"> + <span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        props: {
            hasSlot: {  // 
                type: Boolean,
                default: false
            },
            isList: {  // 
                type: Boolean,
                default: false
            },
            isNull: {  // 
                type: Boolean,
                default: false
            },
            tableData: { // 
                type: Array,
                default () {
                    return []
                }
            }
        },
        data() {
            return {}
        }
    }
</script>
<style></style>


