<template>
    <div>        
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th width="120"></th>
                    <th width="120"></th>
                    <th width="120"></th>
                    <th width="80"></th>
                    <th></th>
                </tr>
            </thead>
            <tbody v-if="!hasSlot && !isNull">
                <tr>
                    <td colspan="5" class="bg-danger text-center"> + </td>
                </tr>
                <tr><td colspan="5"></td></tr>

                <tr>
                    <td colspan="5" class="bg-danger text-center"> +   <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <tr v-if="isPaging">
                    <td></td><td>page</td><td>Y</td><td>Number</td><td></td>
                </tr>
                <tr v-if="isPaging">
                    <td></td><td>rows</td><td>N</td><td>Number</td><td>10</td>
                </tr>
                <tr v-if="isPaging"><td colspan="5"></td></tr>
                <tr v-for="item in tableData">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.required}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
                <tr>
                    <td colspan="5" class="bg-danger text-center"> +  <span v-if="isList">&ltlist&gt</span> </td>
                </tr>
            </tbody>
            <tbody v-if="hasSlot && !isNull">
                <tr>
                    <td colspan="5" class="bg-danger text-center"> + </td>
                </tr>
                <tr><td colspan="5"></td></tr>
                <tr>
                    <td colspan="5" class="bg-danger text-center"> + <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <slot></slot>
                <tr>
                    <td colspan="5" class="bg-danger text-center"> + <span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
            <tbody v-if="isNull">
                <tr><td colspan="5" class="text-center"></td></tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        props: {
            isNull: {
                type: Boolean,
                default: false
            },
            isList: {
                type: Boolean,
                default: false
            },
            isPaging: {
                type: Boolean,
                default: false
            },
            hasSlot: {
                type: Boolean,
                default: false
            },
            tableData: {
                type: Array,
                default () {
                    return [
                        {chnName: '', name: 'custIdType', required: 'Y', type: 'String', remarks: ''},
                        {chnName: '', name: 'custIdNo', required: 'Y', type: 'String', remarks: ''}
                    ]
                }
            }
        },
        data() {
            return  {

            }
        }
    }
</script>
<style>
    
</style>


