<template>
    <div class="message-head">
        <h3 class="text-center"></h3>
        <h4 class="page-header" id="common-head"></h4>
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in commonHeader">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.required}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                commonHeader: [
                    {chnName: '', name: 'ESB-TRANNO', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '', name: 'ESB-ORISYS', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '', name: 'ESB-ROUTE', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '', name: 'ESB-SIGNATURE', required: 'N', type: 'String', remarks: ''},
                    {chnName: '', name: 'ESB-BRANCHNO', required: 'N', type: 'String', remarks: ''}
                ]
            }
        }
    }
</script>
<style lang="stylus">
    .message-head
        height: 600px
</style>


