import Vue from 'vue'
import Router from 'vue-router'
// 
import Summary from '@/components/start/Summary'
import Intro from '@/components/start/Intro'

// 
import CloudDoc from '@/components/cloud/Doc'
import Appendix from '@/components/cloud/Appendix'
// 
import MessageHead from '@/components/common/MessageHead'
// 
import EquityView from '@/components/equityview/EquityView'
// 
import LoanAccept from '@/components/loan/LoanAccept'
// 
import Repay from '@/components/repay/Repay'
// 
import Survival from '@/components/survival/Survival'
// 
import Expira from '@/components/expira/Expira'
// 
import Contact from '@/components/contact/Contact'
// 
import Grant from '@/components/grant/Grant'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Summary',
      component: Summary
    },
    {
      path: '/start/summary',
      name: 'Summary',
      component: Summary
    },
    {
      path: '/start/intro',
      name: 'Intro',
      component: Intro
    },
    {
      path: '/cloud/doc',  // 
      name: 'CloudDoc',
      component: CloudDoc
    },
    {
      path: '/cloud/appendix',  //  -> 
      name: 'Appendix',
      component: Appendix
    },
    {
      path: '/msgHead',  // 
      name: 'MessageHead',
      component: MessageHead
    },
    {
      path: '/contact/doc',  // 
      name: 'Contact',
      component: Contact
    },
    {
      path: '/loan/doc',  // 
      name: 'LoanAccept',
      component: LoanAccept
    },
    {
      path: '/repay/doc',  // 
      name: 'Repay',
      component: Repay
    },
    {
      path: '/survival/doc',  // 
      name: 'Survival',
      component: Survival
    },
    {
      path: '/expira/doc',  // 
      name: 'Expira',
      component: Expira
    },
    {
      path: '/grant/doc',  // 
      name: 'Grant',
      component: Grant
    },
    {
      path: '/equityview/doc',  // 
      name: 'EquityView',
      component: EquityView
    }
  ]
});



